import userList from '@/components/index/userList';
export default {
  data() {
    return {
      userId: '',
      userType: PARENT,
      chooseType: '宝宝'
    }
  },
  created() {
    // //获取localStorage
    this.userList = JSON.parse(localStorage.getItem('USERLIST'));
    this.userId = this.userList.list[0].parentId;
    this.setLocalStorage(this.userList.list[0]);
  },
  methods: {
    getUserList() {
      this.$refs.userList.setShowFlags();
    },
    getData(data) {
      this.userId = data.parentId;
      this.setLocalStorage(data);
    },
    setLocalStorage(user) {
      localStorage.setItem('CURRENT_USER', JSON.stringify(user));
    }
  },
  components: {
    userList
  }
};



