import BScroll from 'better-scroll'
export default {
  data() {
    return {
      showFlag: true,
      iconShowFlag: true, //选择icon显示
      listShowFlag: false, //选择列表显示
      userList: [],
      selectItem: 0,
    }
  },
  props: {
    userType: {
      Number,
      default: 0
    },
    chooseType: {
      String,
      default: ''
    }
  },
  created() {
    //获取localStorage
    this.userList = JSON.parse(localStorage.getItem('USERLIST'));
    // 默认显示第一个
    this.$emit('userData',this.userList.list[0]);
  },
  computed: {
    // 当前页面路由地址
    currentRouter() {
      return this.$store.state.route.path;
    }
  },
  watch: {
    // 监听页面是否为首页,首页则需要显示用户列表
     'currentRouter':function(val){
       switch(val) {
           case '/parent/parentIndex':
           case '/principal/principalIndex':
           case '/teacher/teacherIndex':
            this.showFlag = true;
            break;
          default:
            this.showFlag = false;
            break;
       }
      }
  },
  methods: {
    showUserList() {
      this.setShowFlag();
      this.$nextTick(() => {
          this.scroll = new BScroll(this.$refs.userListWrapper,{
            click: true,
          });
      });
    },
    chooseUser(user,index) {
      this.$emit('userData', user);
      this.selectItem = index;
      this.setShowFlag();
    },
    setShowFlag() {
      if(!this.iconShowFlag) {
        setTimeout(() => {
          this.iconShowFlag = !this.iconShowFlag;
        },200);
      }else {
        this.iconShowFlag = !this.iconShowFlag;
      }
      this.listShowFlag = !this.listShowFlag;
    }
  }
}



